  getEnfrentamientoPorJornada(jornada, liga): Enfrentamiento[] {
    console.log('En enfrentamientos');
    console.log(this.enfrentamientos);
    return this.enfrentamientos.filter(enfrentamiento => (enfrentamiento.jornada == jornada && enfrentamiento.liga == liga));
  }

  setCurrentEnfrentamiento(id){
    this.currentEnfrentamiento = this.enfrentamientos.find(enf => enf.id == id);
    console.log('set');
    console.log(this.currentEnfrentamiento);
    this.enfrentamientoSubject.next(this.currentEnfrentamiento);
  }

  getCurrentEnfrentamiento(): Enfrentamiento{
    console.log('get');
    console.log(this.currentEnfrentamiento);

    return this.currentEnfrentamiento;
  }
